/* styles.css */

/* Estilo general del contenedor de la televisión */
.tv-container {
    position: relative;
    width: 90%;
    max-width: 800px;
    height: auto;
    background-color: #f6c1c1; /* Rosa claro por defecto (modo claro) */
    border: 15px solid #e1a8a8; /* Rosa oscuro por defecto */
    border-radius: 15px;
    box-shadow: 0 0 25px rgba(0, 0, 0, 0.6);
    padding: 15px;
    margin: 0 auto;
    box-sizing: border-box;
    transition: background-color 0.3s, border-color 0.3s;
}

.dark-mode .tv-container {
    background-color: #333; /* Fondo oscuro (modo oscuro) */
    border-color: #666; /* Borde oscuro (modo oscuro) */
}

.dark-mode .video {
    border: 0; /* Quitar borde del video en modo oscuro */
}

/* Estilo del marco del video */
.tv-frame {
    position: relative;
    width: 100%;
    padding-top: 56.25%;
    background: black;
    border-radius: 10px;
    overflow: hidden;
}

/* Estilo del iframe del video */
.video {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 0;
}

/* Estilo de los controles */
.tv-controls {
    position: absolute;
    bottom: 20px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    justify-content: center;
    gap: 15px;
}

/* Estilo del botón "Next Video" */
.tv-controls .next-video-btn {
    background-color: #005f6b;
    color: white;
    border: none;
    padding: 10px 20px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s, transform 0.3s;
}

.tv-controls .next-video-btn:hover {
    background-color: #003f4f;
    transform: scale(1.05);
}

/* Estilo del botón de modo oscuro/claro */
.dark-mode-toggle {
    position: fixed;
    top: 15px;
    right: 15px;
    background-color: #005f6b;
    color: white;
    border: none;
    padding: 10px;
    border-radius: 8px;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 0.3s, transform 0.3s;
    z-index: 1000;
}

.dark-mode-toggle:hover {
    background-color: #003f4f;
    transform: scale(1.05);
}

/* Media Queries para pantallas más pequeñas */
@media (max-width: 1200px) {
    .tv-container {
        width: 85%;
        margin-right: 30px;
    }
}

@media (max-width: 992px) {
    .tv-container {
        width: 80%;
        margin-right: 40px;
    }
}

@media (max-width: 870px) {
    .tv-container {
        width: 75%;
        margin-right: 50px;
        padding: 10px;
    }

    .tv-controls {
        display: none; /* Oculta los botones de control */
    }
}

@media (max-width: 770px) {
    .tv-container {
        width: 80%;
        margin-right: 60px;
    }

    .tv-controls .next-video-btn {
        margin: 0;
        padding: 8px 16px;
        font-size: 14px;
    }

    .dark-mode-toggle {
        font-size: 22px;
        padding: 8px;
    }
}

@media (max-width: 600px) {
    .tv-container {
        width: 100%;
        padding: 5px;
        margin-right: 70px;
    }

    .tv-frame {
        padding-top: 56.25%;
    }

    .tv-controls {
        bottom: 5px;
        gap: 10px;
    }

    .tv-controls .next-video-btn {
        padding: 5px 10px;
        font-size: 12px;
    }

    .dark-mode-toggle {
        font-size: 20px;
        padding: 6px;
    }
}
